  align-items: center;
  column-gap: .15cm;
  margin-top: .2cm;
}

.cmnt-form {
  width: 100%;
  background-color: transparent;
  color: white;
  border: 2px solid #424242;
  border-radius: 5px;
  padding: .1cm .15cm;

  &:focus {
    outline: none;
    border: #616161 2px solid;
  }
}

.img-div-pp {
