}

.heart-pp {
  width: .5cm;
  height: .5cm;
  cursor: pointer;
}

.followers {
  font-size: 15px;
  color: #757575;
  cursor: pointer;
}

.follow-button {
  margin-top: .3cm;
  background-color: transparent;
  border: 2px solid #424242;
  padding: .15cm .5cm;
  width: 100%;
  border-radius: 8px;
  cursor: pointer;
  transition: .25s;

  &:hover {
    background-color: #424242;

  }
}

.profile-heading {
  display: flex;
  align-items: center;
  column-gap: .3cm;
}

.home-box {
  margin-top: 1cm;
  row-gap: .5cm;
